body{font-family:'Open Sans',sans-serif;font-size:18px;color:#010000;font-weight:400;font-style:normal}a{text-decoration:none;cursor:pointer;-webkit-transition:.3s ease-in-out;-moz-transition:.3s ease-in-out;transition:.3s ease-in-out}a:hover{color:#ff1755;-webkit-transition:.3s ease-in-out;-moz-transition:.3s ease-in-out;transition:.3s ease-in-out}header{position:relative;display:block;width:100%;background:#010000;color:#fff;height:90px}header .logo{position:absolute;top:50%;left:15px;-webkit-transform:translateY(-50%);-moz-transform:translateY(-50%);transform:translateY(-50%)}header nav,header nav ul,header nav ul li{margin:0;padding:0}header nav{position:absolute;top:50%;right:15px;-webkit-transform:translateY(-50%);-moz-transform:translateY(-50%);transform:translateY(-50%)}header nav ul li{list-style-type:none;display:inline-block;margin-right:20px}header nav ul li.last{margin:0}header nav ul li a{font-size:19px;line-height:normal;color:#fff}